import { BpmnModel } from "../BpmnModel";
import { BpmnModelHelper } from "../BpmnModelHelper";
import { MexExpression } from "./MexExpression";
import { VariableExpressionDirection } from "./VariableExpressionDirection";



export class VariableExpression extends MexExpression {
    constructor(model: BpmnModel) {
        super(model);

        this.Direction = VariableExpressionDirection.ActivityDataInputAssociation;
    }

    readonly ClrTypeID: string = "bpmn.Mex.VariableExpression";
    NewInstance(): VariableExpression {
        return new VariableExpression(this.Model);
    }

    Direction: string;
    SourceRef: string;

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.Direction = null;
        this.SourceRef = null;
    }

    ConvertToJsonObject(): any {
        let obj = super.ConvertToJsonObject();

        BpmnModelHelper.SetAttributeValue(obj, "SourceRef", this.SourceRef);
        BpmnModelHelper.SetAttributeValue(obj, "Direction", this.Direction);

        return obj;
    }

    LoadFromJsonObject(obj: any): void {
        super.LoadFromJsonObject(obj);

        this.SourceRef = obj.SourceRef;
        this.Direction = obj.Direction;
    }
}